.error-indicator {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	color: #c78f22;
}

.error-indicator__img {
	margin-bottom: 1rem;
	width: 245px;
	height: 150px;
	border-radius: 10px;
}

.error-indicator .boom {
	font-size: 1.7rem;
}
